import React from 'react'
import { useState, useEffect } from 'react'
import axios from 'axios'
import { Container} from 'react-bootstrap'
import {BASE_API_URL} from './BaseApi'


function FetchingData() {
    const [facts, setFacts] = useState(null);
    const [idFromButtonClick, setIdFromButtonClick] = useState(Math.floor(Math.random()*6))

    const generateNumber = () => {
        let number = Math.floor(Math.random()*6);
        console.log(number)
        return number
    }

    const handleClick = () => {
        setIdFromButtonClick(generateNumber())
    }
    

    // useEffect(()=>{
    //     axios.get('facts.json')
    //     .then(response => {
    //         console.log(response)
    //         setFacts(response.data.list)
    //     })
    // }, [])

    useEffect(()=>{
        axios.get(`${BASE_API_URL}/sql`)
        .then(response => {
            console.log(response)
            console.log(response.data.names)
            setFacts(response.data)
        })
    }, [])

    return (
        
        <Container className="d-flex justify-content-center align-items-center" style={{minHeight: "100vh"}}>
            <div>
                <div className="d-flex justify-content-center badge bg-secondary text-wrap">
            <div>Fact of the day.
            <div>
                {facts && facts[idFromButtonClick].names}
            </div>
            </div>
                </div>
            <div>
               <div className="my-5 text-center">
                    <button type="button" className="btn btn-success btn-lg" onClick={handleClick}>Random fact or meme</button>
                </div>
            </div>
            </div>
        </Container>
    )
}

export default FetchingData